/* Universial */
.text1{
    font-size: 30px;
    text-align: center;
    color: white
}
.header{
    text-align: left;
    margin-left: 2px;
    color: white
}

/* Spinner */
@keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
}

.loading-spinner {
    width: 50px;
    height: 50px;
    margin: 0 auto;
    border: 10px solid #f3f3f3; 
    border-top: 10px solid #383636; 
    border-radius: 50%;
    animation: spinner 1.5s linear infinite;
}

/* Weather.js */
.weatherContainer{
    display: flex;
    align-items: center;
    justify-content: center;
}

.weatherContainer2{
    border-radius: 25px;
    width: 300px;
    height: 300px;
    background-color: rgb(92, 0, 141);
    border: 1px solid white;
    box-shadow:  5px 5px 20px rgba(0, 0, 0, 0.5);
}

.weatherDataContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}

.weatherDataContainer2{
    height: 200px;
    margin: 0 auto;
}

.weatherHeader{
    margin: 0;
    font-weight: bold;
    font-size: 25px;
    text-align: center;
    color: white
}

.weatherText{
    margin: 0;
    font-size: 20px;
    text-align: center;
    color: white
}

.weatherIconContainer{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 75%;
    width: 100%;
}

/*  Forecast.js */
.forecastContainer{ 
    display: grid;
    grid-template-columns: 33% 33% 33%;
}

.forecast{
    height: 150px;
    border: 2px solid white;
    border-radius: 25px;
    margin: 5px;
    padding: 5px;
    background-color: rgb(92, 0, 141);
    border: 1px solid white;
    box-shadow:  5px 5px 20px rgba(0, 0, 0, 0.5);
}

.leftContainer{
    float: left;
    width: 50%;
    height: 100%;
}

.rightContainer{
    float: left;
    width: 50%;
    height: 100%;
}

.forecastData{
    text-align: left;
    padding-left: 10px;
    font-size: 18px;
    color: white;
}